.blockTitle{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 20px;
}
.title {
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  letter-spacing: 5px;
  font-weight: 500;
  font-size: 36px;
  text-transform: capitalize;
  color: #f89800;
}
.line{
  width: 55px;
  height: 3px;
  color: rgba(248, 152, 0, 0.55);
  margin: 20px 0;
}